import axios, { defaults } from 'axios';
import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import Navbar from 'components/navbar'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'

import { Pagination, Message, Dialog } from 'element-ui'
import '../../theme/element-variables.scss'

import './index.less'
import Notice from './notice'

Vue.use(Pagination)
Vue.use(Dialog)
Vue.prototype.$message = Message;
Vue.config.productionTip = false

defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
defaults.xsrfHeaderName = 'X-CSRFTOKEN'
defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/feature_notice' : '/static/w/pages/notice.html',
  routes: [
    { path: '/', component: Notice },
    { path: '*', redirect: '/' },
  ],
  linkActiveClass: 'active',
})

const store = new Vuex.Store({
  state: {
    teacher: null,
    accessToken: null,
  },
  getters: {
    school: (state) => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
    },
    updateAccessToken(state, payload) {
      state.accessToken = payload.accessToken
    },
  },
})

router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then(response => {
      const { teacher, accessToken } = response.data
      store.commit('updateAccessToken', { accessToken: `Bearer ${accessToken}` })
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
})

new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: { Navbar },
})



