<template>
  <div id="notice">
    <template v-if="notices.length > 0">
      <div class="contentBox">
        <a v-for="({id, title}) in notices" :key="id" class="item" href="javascript:void(0)" @click="getDetail(id)">
          {{title}}
        </a>
      </div>
      <el-pagination v-if="this.total >= 10" layout="prev, pager, next" :total="total" @current-change="currentChangeHandle" :current-page.sync="currentPage" :page-size="limit">
      </el-pagination>
    </template>
    <div v-else class="empty">
      暂无通知
    </div>
    <el-dialog :title="notice.title" :visible.sync="visible">
      {{notice.content}}
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'

const PAGE_SIZE = 10

export default {
  data() {
    return {
      notices: [],
      start: 0,
      total: 0,
      limit: PAGE_SIZE,
      notice: {},
      currentPage: 1,
      visible: false,
    }
  },
  mounted() {
    this.getNotices()
  },
  computed: {
    ...mapState({ accessToken: 'accessToken' }),
  },
  methods: {
    getNotices(start = 0, limit = PAGE_SIZE) {
      const params = { start, limit }
      axios.get('/teacher/api/feature_notices/', {
        headers: { Authorization: this.accessToken },
        params,
      }).then(({ data }) => {
        const { notices, total } = data
        this.notices = notices
        this.total = total
      }).catch(() => {
        this.$message.error('请求错误')
      })
    },
    getDetail(id) {
      const url = `/teacher/api/feature_notices/${id}`
      axios.get(url, {
        headers: { Authorization: this.accessToken },
      }).then(({ data }) => {
        this.notice = data.notice
        this.$nextTick(() => {
          this.visible = true
        })
      }).catch(() => {
        this.$message.error('请求错误')
      })
    },
    currentChangeHandle(pageCount) {
      const start = (pageCount - 1) * PAGE_SIZE
      this.getNotices(start)
    },
  },
}
</script>

<style lang="less" scoped>
@border-color: #ddd;

#notice {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  .contentBox {
    border-radius: 2px;
    margin: 40px;
    width: 65vw;
    max-width: 1500px;
    background: #fff;
    box-shadow: 0 0 10px 1px @border-color;
    .item {
      font-size: 18px;
      margin: 0 40px;
      padding: 20px 3px;
      border-bottom: 1px solid @border-color;
      color: #42a5f5;
      display: block;
      line-height: 1.3;
      &:last-child {
        border: none;
      }
      &:hover {
        color: #66ccff;
      }
    }
  }
  .empty {
    font-size: 20px;
    padding: 40px;
    font-weight: bold;
  }
  .el-pagination {
    display: flex;
    justify-content: center;
    padding: 0;
    margin-bottom: 40px;
    border: 1px solid @border-color;
  }
}
</style>




